import './App.css';
import 'react-toastify/dist/ReactToastify.css';

import { ConnectedRouter } from 'connected-react-router';
import React, { lazy, Component } from 'react';
import { connect } from 'react-redux';
import { Route, Switch } from 'react-router';
import { push } from 'react-router-redux';
import { Slide, ToastContainer, toast } from 'react-toastify';
import './App.scss';
// import CircularProgress from '@material-ui/core/CircularProgress';
import agent from './agent';
// import Loader from './common/Components/Loader';

import {
  APP_LOAD, REDIRECT, SET_USER,
} from './constants/actionTypes';
import { store } from './store';
import { WEB_TOKEN } from './constants/otherConstants';
import CircularProgress from '@material-ui/core/CircularProgress';

import AdminPage from './components/AdminPage/AdminPage';
import LandingPage from './components/LandingPage/LandingPage';
import Pricing from './components/LandingPage/components/Pricing';
import PrivacyPolicy from './components/LandingPage/components/PrivacyPolicy';
import Login from './components/Auth/Login/Login';
import Signup from './components/Auth/SignUp/Signup';
import FAQ from './components/LandingPage/components/FAQ';
import Purchase from './components/Purchase/Purchase';
import PurchaseOutput from './components/Purchase/PurchaseOutput';
import OrderHistory from './components/OrderHistory/OrderHistory';
import ProductsPage from './components/ProductsPage/ProductsPage';
import TermsAndConditions from './components/LandingPage/components/TermsAndConditions';
import Payment from './components/HomePage/components/Payment';
import SuccessPayment from './components/HomePage/components/SuccessPayment';
import FailedPayment from './components/HomePage/components/FailedPayment';
import Payments from './components/Payments/Payments';
import SoldProducts from './components/SoldProducts/SoldProducts';
import ContactUs from './components/LandingPage/components/ContactUs';
import RefundPolicy from './components/LandingPage/components/RefundPolicy';
import ChangeHeader from './components/AdminPage/components/ChangeHeader';
import MyAccount from './components/AdminPage/components/MyAccount';
// import SideDrawer from './components/AdminPage/components/SideDrawer';

const HomePage  = lazy(()=>import('./components/HomePage/HomePage'));

toast.configure();
class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // openSideDrawer: false,
      // user: {},
    };
  }

  componentWillMount() {
    const token = window.localStorage.getItem(WEB_TOKEN);
    if (token == null) {
      // store.dispatch(replace('/'));
      // this.props.onRedirect('/');
    } else {
      agent.setToken(token);
    }
    // const { history } = this.props;

    // history.listen((newLocation, action) => {
    //   if (action === 'PUSH') {
    //     if (
    //       newLocation.pathname !== this.currentPathname
    //       || newLocation.search !== this.currentSearch
    //     ) {
    //       // Save new location
    //       this.currentPathname = newLocation.pathname;
    //       this.currentSearch = newLocation.search;

    //       // Clone location object and push it to history
    //       history.push({
    //         pathname: newLocation.pathname,
    //         search: newLocation.search,
    //       });
    //       history.push({
    //         pathname: newLocation.pathname,
    //         search: newLocation.search,
    //       });
    //       history.push({
    //         pathname: newLocation.pathname,
    //         search: newLocation.search,
    //       });
    //     }
    //   }
    // });
    // if (this.props.user) this.setState({ user: this.props.user });
    this.props.onLoad(token ? 1 : null, token);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.history.location !== nextProps.history.location) {
      this.props.onRedirect(nextProps.history.location);
    }
    if (nextProps.redirectTo && nextProps.redirectTo !== this.props.redirectTo) {
      store.dispatch(push(nextProps.redirectTo));
    }
    if (nextProps.user) this.setState({ user: nextProps.user });
  }

  render() {
    let sideDrawer;
    let backdrop;
    return (
      <div style={{ height: '100%' }}>
        
        <React.Suspense fallback={(
            <div style={{ width: '40px', height: '40px',position:'fixed', top:'50%',left:'50%', transform:'translate(-50%,-50%)' }}>
              <h4>Loading...</h4>
              <CircularProgress  />
            </div>
          )}
        >
        {/* <SideDrawer/> */}
          <ConnectedRouter history={this.props.history}>
            <Switch>
              <Route path="/" component={LandingPage} exact />
              <Route path="/privacy-policy" component={PrivacyPolicy} exact />
              <Route path="/refund-policy" component={RefundPolicy} exact />
              <Route path="/terms-and-conditions" component={TermsAndConditions} exact />
              <Route path="/login" component={Login} exact />
              <Route path="/signup" component={Signup} exact />
              <Route path="/admin" component={AdminPage} exact />
              <Route path="/faqs" component={FAQ} exact />
              <Route path="/contact-us/" component={ContactUs} exact />
              <Route path="/purchase" component={Purchase} exact />
              <Route path="/purchase/result" component={PurchaseOutput} exact />
              <Route path="/order-history/" component={OrderHistory} exact />
              <Route path="/admin/products/" component={ProductsPage} exact />
              <Route path="/admin/products/sold-products/" component={SoldProducts} exact />
              <Route path="/admin/payments/" component={Payments} exact />
              {/* <Route path="/account" component={MyAccount} exact /> */}
              <Route path="/pricing" component={Pricing} exact />
              {/* <Route path='/forgot-password' component={ForgotPassword} exact/> */}
              <Route path="/user-payment/" component={Payment} exact />
              <Route path="/user-payment/success/" component={SuccessPayment} exact />
              <Route path="/user-payment/fail/" component={FailedPayment} exact />
              <Route path="/:userName/" component={HomePage} exact />

              <Route path="*">
                <NoMatch />
              </Route>
            </Switch>
          </ConnectedRouter>
        </React.Suspense>
        {sideDrawer}
        {backdrop}
        <ToastContainer
          autoClose={2000}
          closeOnClick
          transition={Slide}
          pauseOnFocusLoss={false}
          position="top-right"

        />
        <div style={{dispaly:'none'}} > <ChangeHeader/> </div>
        <div style={{dispaly:'none'}} > <MyAccount/> </div>
      </div>
    );
  }
}
function NoMatch() {
  return (
    <div style={{minHeight:'100vh', display:'flex' , flexDirection:'column',justifyContent:'center', alignItems:'center'}} >
      <center>      
        <h1>
          404 Page not found
        </h1>
        
           <a href='/' >Go To Home</a>
        
      </center>
    </div>
  );
}

const mapStateToProps = (state) => ({
  appLoaded: state.common.appLoaded,
  appName: state.common.appName,
  currentUser: state.common.currentUser,
  redirectTo: state.common.redirectTo,
  apiKey: state.common.apiKey,
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
  onLoad: (payload, apiKey) => dispatch({
    type: APP_LOAD,
    payload,
    apiKey,
    skipTracking: true,

  }),
  onRedirect: (route) => dispatch({ type: REDIRECT, payload: route }),
  clearReducer: () => dispatch({ type: 'CLEAR' }),
  setUser: (user) => dispatch({ type: SET_USER, payload: user }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);
